export default {
  props: {
    councilorMap: {
      type: Object,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    sayList: {
      type: Array,
      required: true,
      validator (list) {
        return list.every(sayit => sayit.councilorId && sayit.summary)
      }
    },
    hasMore: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    showMore () {
      this.$emit('more')
    }
  }
}
